.country {
    border: 1.5px solid magenta;
    background-color: rgba(74, 70, 64, 0.387);
    width: 30%;
    aspect-ratio: 1/1;
    margin: 1%;
    border-radius: 35px 0 35px 0;

    .country__title {
        width: fit-content;
        margin: 0 auto;
        font-size: 25px;
        text-decoration: underline;
        cursor: pointer;
    }

    .country-info {
        width: 95%;
        margin: 5px auto;
        text-align: start;

        .country__capital {}

        .country__area {
            .km-square {
                position: relative;

                &::before {
                    position: absolute;
                    content: "2";
                    font-size: 10px;
                    top: -5px;
                    right: -8px;
                }
            }
        }
    }

    .flag {

        width: 95%;
        aspect-ratio: 3/2;
        margin: 5px auto;

        .flag-img {
            width: 100%;
            height: 100%;
        }
    }
}